<template>
  <div id="dialog-window">
    <div id="header">
      Bottles
    </div>
    <div id="scrollable-content">
      <virtual-scroller page-mode class="virtual-list" :items="items || []" item-height="24" content-tag="ul">
        <template slot-scope="props">
          <li :key="props.itemKey">{{props.item._id}} es donde: {{props.item.ubicacion}}. Entregas {{props.item.ent}}</li>
        </template>
      </virtual-scroller>
    </div>

  </div>
</template>

<script>

  export default {
    pouch: {
      items() {
        return {
          database: 'bottles', // you can pass a database string or a pouchdb instance
          selector: {
            _id: { $regex: /IBA/i },
            ent: { $gt: 0 },
          },
          // selector: { _id: 'CLAA015' },
        };
      },
      // items: { /* All rows */ },
    },
  };
</script>


<style>
  #dialog-window {
    height: 500px;
    padding: 10px;
    /*padding-bottom: 35px;*/
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 15px;
    border: 1px black solid;
  }

  #scrollable-content {
    height: 450px;
    overflow: auto;
  }

  #header {
    height: 20px;
  }
</style>



