import PouchDB from 'pouchdb';
import finder from 'pouchdb-find';
import liveFinder from 'pouchdb-live-find';
// import authenticator from 'pouchdb-authentication';

const LG = console.log; // eslint-disable-line no-console, no-unused-vars

PouchDB.plugin(finder);
PouchDB.plugin(liveFinder);
// PouchDB.plugin(authenticator);

const ldbBottles = new PouchDB('bottles');
const rdbBottles = new PouchDB('https://yourdb.yourpublic.work/bottles');

ldbBottles.sync(rdbBottles, { live: true })
  .on('change', (repl) => {
    LG(`Database replication: ${repl.direction} ${repl.change.docs.length} records.`);
    LG(repl);
  })
  .on('error', err => LG(`Database error ${err}`));

LG(`
=============================================================`);

ldbBottles.createIndex({
  index: {
    fields: ['ultimo'],
    name: 'idxUltimo',
  },
}).then((result) => {
  LG(`New index ${result}`);
  LG(result);
}).catch((err) => {
  LG(`Index creation failure: ${err}`);
  LG(err);
});

ldbBottles.liveFind({
  selector: { ubicacion: 'planta' },
  sort: [{ ultimo: 'desc' }, { _id: 'desc' }],
  aggregate: true,
}).on('update', (update, aggregate) => {
  // update.action is 'ADD', 'UPDATE', or 'REMOVE'
  // update also contains id, rev, and doc
  if (update.action !== 'ADD') { LG(update.action, update.id, aggregate); }
  // aggregate is an array of docs containing the latest state of the query
  // refreshUI(aggregate);
  // (refreshUI would be a function you write to pipe the changes to your rendering engine)
});

ldbBottles.get('CLAA017').then(doc => LG(doc));

export default ldbBottles;



